<template>
  <div id="distributionRecord">
    <header>
      累计收益：￥
      <span>115.00</span>
    </header>

    <van-pull-refresh
      v-model="pullLoading"
      success-text="刷新成功"
      @refresh="pullRefresh"
      :head-height="100"
      :disabled="disablePullRefresh"
    >
      <van-list
        v-model="pushLoading"
        :immediate-check="false"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="distributionList">
          <div class="distributionBox" v-for="(item) in 10" :key="item">
            <p>
              <span>订单编号</span>
              <span>11995665</span>
            </p>
            <p>
              <span>订单时间</span>
              <span>2020-06-12 12:13</span>
            </p>
            <p>
              <span>商品</span>
              <span>《生活大爆炸》</span>
            </p>
            <p>
              <span>订购人</span>
              <span>用户微信昵称</span>
            </p>
            <p>
              <span>成单价格</span>
              <span>￥100.00</span>
            </p>
            <p>
              <span>邀请奖励</span>
              <span>50%（￥50.00）</span>
            </p>
          </div>
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import { getdistributionData } from "../assets/javascript/apis.js";
export default {
  data() {
    return {
      pullLoading: false,
      disablePullRefresh: false,
      getDataFlag: true,
      pushLoading: false,
      finished: false,
      dataList: [],
      page: 1,
    };
  },
  created() {
    this.getData();
  },
  methods: {
    //获取页面数据
    getData(page, refreshData = true) {
      //控制发起请求的频率
      if (!this.getDataFlag) return;
      this.getDataFlag = false;
      //发起请求前禁用下拉刷新
      this.disablePullRefresh = true;

      const data = {
        page: page ? page : 1,
      };
      getdistributionData().then((res) => {
        console.log(res);
        //处理数据
        if (refreshData) {
          this.dataList = [];
        }
        this.dataList.push(res.data);

        //可以重新发起请求
        this.getDataFlag = true;
        // 加载数据成功，修改下拉刷新的状态
        this.pullLoading = false;
        //重新启用下拉刷新
        this.disablePullRefresh = false;
      });
    },
    //下拉加载
    pullRefresh() {
      this.getData();
    },
    //上拉加载更多
    onLoad() {
      this.page = this.page + 1;
      this.getData(this.page, false);
    },
  },
};
</script>
<style lang="scss" scoped>
#distributionRecord {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  header {
    width: 100%;
    height: 42px;
    text-align: center;
    line-height: 42px;
    background: rgba(75, 165, 245, 1);
    font-size: 14px;
    color: rgba(255, 255, 255, 1);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
  }
  /deep/.van-pull-refresh {
    flex-grow: 1;
    overflow: visible;
    .van-pull-refresh__track {
      .van-pull-refresh__head {
      }
    }
  }
  .distributionList {
    width: 100%;
    margin-top: 42px;
    padding: 15px 15px 0;
    box-sizing: border-box;
    // background: red;
    .distributionBox {
      width: 100%;
      height: 205px;
      margin-top: 15px;
      padding: 20px 15px;
      box-sizing: border-box;
      background: rgba(242, 244, 246, 1);
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      &:first-child {
        margin-top: 0;
      }
      p {
        font-size: 14px;
        color: rgba(7, 7, 7, 1);
        display: flex;
        justify-content: space-between;
      }
    }
  }
}
</style>